// 定义操作数据库
var db = wx.cloud.database()
var users = db.collection('user')
//引入登录模态框
import motal from '../../../element/model.vue'
// 引入即可反馈组件
import HMmessages from "@/components/HM-messages/HM-messages.vue"
export default{
	name:'messages',
	props:{
		
	},
	components:{
		motal,
		HMmessages
	},
	data(){
		return {
			num: 0,
			newmessage:[
				'全部',
				'物美价廉'
			],
			avatarUrl:'',		//用户头像
			nickName:'',		//用户昵称
			box:false
		}
	},
	methods:{
		menubtn(index,item){
			this.num = index
		},
		
		//点击评论显示评论框
		popup(){
			//用户登录之后才能评论
			users.get()
			.then((res)=>{
				console.log(res)
				//数据库中有数据则说明已登录
				if(res.data.length == 0){
					console.log('用户尚未登录')
					let message = '请登陆后再操作'
					this.$nextTick(()=>{
						this.$refs.mon.init(message)
					})
					
				}else{
					console.log('用户已登录')
					let usermen = res.data[0]
					this.avatarUrl = usermen.avatarUrl
					this.nickName = usermen.nickName
					this.box = true
				}
			})
			.catch((err)=>{
				console.log('登录错误')
			})
		},
		//隐藏评论框
		messcancel(){
			this.box = false
		}
	},
}
